.header {
  padding-top: 30px;
}

.header .header__home {
  max-width: 200px;
}

@media only screen and (min-width: 980px) {
  .header .header__home {
    max-width: 300px;
  }
}

.header .mobile-nav-trigger__container {
  flex-grow: 1;
  text-align: right;
}

@media only screen and (min-width: 980px) {
  .header .mobile-nav-trigger__container {
    display: none;
  }
}

.header .mobile-nav-trigger__container .mobile-nav-trigger {
  padding: 15px 0;
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  color: var(--cool-grey);
}

.header .mobile-nav-trigger__container .mobile-nav-trigger:hover, .header .mobile-nav-trigger__container .mobile-nav-trigger:focus {
  text-decoration: none;
}

.header .navigation {
  flex-grow: 1;
  text-align: right;
  padding-top: 30px;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: -150%;
  background: white;
  width: 100%;
  z-index: 100;
  transition: all 0.3s ease-in-out;
  padding: 0 20px;
}

body.mobile-nav-active .header .navigation {
  left: 0;
}

@media only screen and (min-width: 980px) {
  .header .navigation {
    position: static;
    background: none;
    width: auto;
    padding: 25px 0 0 0;
  }
}

.header .navigation .mobile-nav-trigger {
  padding: 35px 0 20px 25px;
  display: inline-block;
  font-size: 18px;
  font-weight: 700;
}

.header .navigation .mobile-nav-trigger:hover, .header .navigation .mobile-nav-trigger:focus {
  text-decoration: none;
}

@media only screen and (min-width: 980px) {
  .header .navigation .mobile-nav-trigger {
    display: none;
  }
}

.navigation__list {
  margin: 0;
  padding-top: 100px;
  list-style-type: none;
}

@media only screen and (min-width: 980px) {
  .navigation__list {
    padding: 0;
  }
}

@media only screen and (min-width: 980px) {
  .navigation__list-item {
    display: inline-block;
  }
}

.navigation__item {
  display: inline-block;
  color: #72837e;
  font-weight: 300;
  font-size: 34px;
  transition: all 0.2s;
  position: relative;
  line-height: 1.7;
}

@media only screen and (min-width: 980px) {
  .navigation__item {
    font-weight: 700;
    font-size: 20px;
    padding: 10px 20px;
  }
}

@media only screen and (min-width: 980px) {
  .navigation__item::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 5px;
    background: transparent;
  }
}

.navigation__item.active, .navigation__item:hover, .navigation__item:focus {
  text-decoration: none;
  color: #d74247;
}

@media only screen and (min-width: 980px) {
  .navigation__item.active::after {
    opacity: 0.1;
    background-color: var(--faded-red);
  }
}

.footer {
  background: #90a19c;
  color: white;
  padding: 50px 0;
}

.footer .wrapper {
  flex-wrap: wrap;
}

.footer .footer__company {
  width: 100%;
  font-size: 12px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 2.6px;
  color: var(--cloudy-blue);
  text-transform: uppercase;
  padding: 20px 0;
}

.footer a {
  color: white;
}

.footer .footer-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 50%;
}

@media only screen and (min-width: 768px) {
  .footer .footer-links {
    width: 25%;
  }
}

.footer .footer-links .footer-nav__item,
.footer .footer-links .legal-nav__item {
  font-size: 18px;
  line-height: 1.3;
}

.footer .footer-links .footer-nav__item {
  font-weight: bold;
}

.footer .footer-disclaimer {
  padding-top: 25px;
}

@media only screen and (min-width: 768px) {
  .footer .footer-disclaimer {
    width: 50%;
    padding-top: 0;
  }
}

.footer .footer-disclaimer p {
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.43;
  letter-spacing: normal;
  color: var(--white);
}

.footer .footer-disclaimer p + p {
  margin-top: 10px;
}

.footer .footer-disclaimer a {
  text-decoration: underline;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: #d74247;
  transition: all 0.2s;
}

a:hover, a:focus {
  text-decoration: underline;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
}

body {
  background: url(../images/dc-background.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  font-family: "Meta Offc", "Lato", sans-serif;
}

body:not(.layout-home) {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, white 10%, white 100%), url(../images/dc-background.jpg);
}

body.mobile-nav-active {
  overflow: hidden;
}

img {
  max-width: 100%;
}

p,
ul,
ol {
  font-weight: 300;
  line-height: 1.5;
  letter-spacing: -0.3px;
  font-size: 20px;
  color: #546560;
}

p {
  margin: 0;
}

p + p {
  margin-top: 25px;
}

.content-section ul {
  list-style-type: none;
}

.content-section ul li {
  position: relative;
}

.content-section ul li::before {
  background: #e49e52;
  content: "";
  position: absolute;
  top: 13px;
  left: -30px;
  height: 5px;
  width: 5px;
  border-radius: 50%;
}

.content-section ul ul li::before {
  background: #90a19c;
}

.hero {
  height: 33vw;
  min-height: 300px;
  max-height: 500px;
  position: relative;
  margin-top: 20px;
  margin-bottom: 100px;
}

@media only screen and (min-width: 980px) {
  .hero {
    margin-bottom: 0;
  }
}

.hero.wrapper {
  padding-right: 0;
  padding-left: 0;
}

.hero.hero-home {
  background: url(../images/hero-images/hero-home.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-about {
  background: url(../images/hero-images/hero-about.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-parents {
  background: url(../images/hero-images/hero-parents.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-community {
  background: url(../images/hero-images/hero-community.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-primary-care {
  background: url(../images/hero-images/hero-primary-care.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-partners {
  background: url(../images/hero-images/hero-partners.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-research {
  background: url(../images/hero-images/hero-research.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-acquire {
  background: url(../images/hero-images/hero-acquire.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero.hero-contact {
  background: url(../images/hero-images/hero-contact.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: 50%;
}

.hero .hero__title {
  position: absolute;
  background: var(--faded-orange);
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  font-size: 32px;
  font-weight: 500;
  line-height: 1.12;
  letter-spacing: -1px;
  margin: 0;
  padding: 20px;
  transform: translateY(100%);
}

@media only screen and (min-width: 980px) {
  .hero .hero__title {
    left: -20px;
    bottom: 30px;
    width: 380px;
    padding: 18px 50px 24px;
    font-size: 52px;
    transform: none;
    letter-spacing: -1px;
  }
}

.hero .hero__title sup {
  font-size: 14px;
  vertical-align: 6px;
}

@media only screen and (min-width: 980px) {
  .hero .hero__title sup {
    font-size: 20px;
    vertical-align: 10px;
  }
}

body.layout-home .hero .hero__title {
  background: var(--cool-grey);
}

.hero-description {
  max-width: 1010px;
  font-size: 20px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.4;
  letter-spacing: -0.3px;
  color: var(--gunmetal);
  padding: 20px 0 35px;
}

.hero-description a {
  font-weight: 500;
}

.content-wrapper {
  padding-top: 0;
  padding-bottom: 50px;
}

@media only screen and (min-width: 980px) {
  .content-wrapper {
    padding-top: 50px;
    padding-bottom: 115px;
  }
