@media only screen and (min-width: 786px) {
  .home-nav-callouts .wrapper {
    justify-content: space-evenly;
  }
}

.home-nav-callouts .nav-callouts__title {
  width: 100%;
  font-size: 26px;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: -0.8px;
  color: var(--faded-red);
  margin-top: 0;
}

@media only screen and (min-width: 980px) {
  .home-nav-callouts .nav-callouts__title {
    font-weight: 400;
    margin-bottom: 0;
    text-align: center;
    font-size: 36px;
  }
}

.home-nav-callouts .nav-callouts__title img {
  width: 100px;
  vertical-align: middle;
}

@media only screen and (min-width: 980px) {
  .home-nav-callouts .nav-callouts__title img {
    width: auto;
