.home-nav-callouts .nav-callout .nav-callout__image {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

.home-nav-callouts .nav-callout .nav-callout__title {
  font-size: 28px;
  line-height: 1.29;
  letter-spacing: -0.6px;
  color: var(--gunmetal);
  margin-top: 21px;
  margin-bottom: 7px;
}

.home-nav-callouts .nav-callout .nav-callout__description {
  font-size: 18px;
  line-height: 26px;
  color: #72837e;
  font-weight: 400;
}

.home-nav-callouts .nav-callout .nav-callout__link {
  font-size: 18px;
  line-height: 26px;
  font-weight: 500;
  white-space: nowrap;
}

.questionnaires {
  background: white url(../images/dc-background.jpg);
  background-size: 100% auto;
  background-repeat: repeat-y;
  text-align: center;
  padding: 50px 0;
}

.questionnaires .wrapper {
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.questionnaires .questionnaires__title {
  font-size: 32px;
  font-weight: 500;
  line-height: 34px;
  letter-spacing: -1px;
  text-align: center;
  color: var(--faded-red);
  width: 100%;
  margin: 0 0 12px 0;
}

.questionnaires .questionnaires__description {
  width: 100%;
  margin-bottom: 50px;
}

.questionnaires .questionnaire__card {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  background: white;
  padding: 25px;
}

.questionnaires .questionnaire__card + .questionnaire__card {
  margin-top: 20px;
}

@media only screen and (min-width: 980px) {
  .questionnaires .questionnaire__card + .questionnaire__card {
    margin-top: 0;
  }
}

@media only screen and (min-width: 980px) {
  .questionnaires .questionnaire__card {
    width: calc(50% - 50px);
  }
}

.questionnaires .questionnaire__card:not(.nutri-estep) .card__link {
  color: #e49e52;
}

.questionnaires .questionnaire__card.nutri-estep .card__link {
  color: #459782;
}

.questionnaires .card__description {
  margin-top: 15px;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
}

.questionnaires .card__link {
  font-weight: 500;
  transition: all 0.2s;
}

.questionnaires .card__link:hover, .questionnaires .card__link:focus {
  text-decoration: underline;
  font-weight: 500;
}

.chapter-nav {
  display: none;
}

@media only screen and (min-width: 980px) {
  .chapter-nav {
    width: 25%;
    max-width: 240px;
    display: block;
    padding: 3px 15px;
    line-height: 1.7;
    list-style: none;
  }
}

.chapter-nav .chapter-nav__item {
  opacity: 0.5;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: -0.3px;
  color: var(--cool-grey);
  transition: all 0.2s;
  cursor: pointer;
}

.chapter-nav .chapter-nav__item:hover, .chapter-nav .chapter-nav__item:focus, .chapter-nav .chapter-nav__item.active {
  color: #d74247;
  text-decoration: none;
}

.content-section {
  flex-grow: 1;
  position: relative;
}

.content-section #back-to-top {
  display: none;
}

@media only screen and (min-width: 980px) {
  .content-section #back-to-top {
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    background: url(../images/arrow.png);
    background-repeat: no-repeat;
    background-position-x: 50%;
    padding-top: 58px;
    opacity: 0.5;
    font-size: 14px;
    font-weight: 700;
    color: var(--cool-grey);
  }
  .content-section #back-to-top:hover, .content-section #back-to-top:focus {
    text-decoration: none;
  }
}

@media only screen and (min-width: 980px) {
  .page-content {
    max-width: 800px;
    width: calc(100% - 100px);
  }
}

.page-content p + .content-heading,
.page-content ul + .content-heading {
  padding-top: 50px;
}

.content-heading {
  font-size: 28px;
  line-height: 34px;
  font-weight: 700;
  letter-spacing: -0.3px;
  color: var(--faded-red);
}

.content-subheading {
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: -0.3px;
  color: var(--gunmetal);
}

.table .table__heading,
.table .table__row {
  display: flex;
}

.table .table__heading {
  border-bottom: 2px solid rgba(144, 161, 156, 0.5);
}

.table .table__row:not(:last-of-type) {
  border-bottom: 1px solid rgba(144, 161, 156, 0.25);
}

.table .heading__cell,
.table .table__cell {
  padding: 5px 15px 5px 0;
  width: 50%;
}

@media only screen and (min-width: 980px) {
  .table .heading__cell,
  .table .table__cell {
    padding: 15px 15px 15px 0;
  }
}

.table .heading__cell {
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: -0.3px;
  color: var(--gunmetal);
}

.table .table__cell {
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: -0.3px;
  color: var(--gunmetal);
}

@media only screen and (min-width: 890px) {
  .table .table__cell {
    font-size: 20px;
    line-height: 1.5;
  }
}
